import { Component, OnInit,Output,Input,EventEmitter,ViewChild,ChangeDetectorRef,ElementRef, Inject} from '@angular/core';
import {FormGroup,FormControl,FormBuilder,Validators } from '@angular/forms';
import {InventorySetupService,InventorySetup, CurrentUserService,ErrorHandleService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxRadioButtonComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxradiobutton';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-inventory-setup-edit',
  templateUrl: './inventory-setup-edit.component.html',
  styleUrls: ['./inventory-setup-edit.component.scss']
})
export class InventorySetupEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('fifo') fifo: jqxRadioButtonComponent;
  @ViewChild('lifo') lifo: jqxRadioButtonComponent;
  @ViewChild('wa') wa: jqxRadioButtonComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Input('isEditData') isEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  inventoryForm:FormGroup;
  isData:InventorySetup;
  apiUrl: string;
  fiscal_data:any;
  fiscalAdapter:any;

  userData: any = {};

 constructor(
    private fb:FormBuilder,
    private isService: InventorySetupService,
    @Inject('API_URL') apiUrl: string,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    private cdr:ChangeDetectorRef,) 
    { 
       this.apiUrl = apiUrl;
       this.userData = this.cus.getTokenData();
       this.createForm();
    }
  ngOnInit() {
    this.isData=new InventorySetup(this.isEditData);
    this.inventoryForm.setValue(this.isData);
    this.fiscal_data =
    {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
            { name: 'id' },
            { name: 'name' },
            ],
            async:false,
            data: this.userData,
            url: this.apiUrl+'fiscal_year?limit=all',
    };
    this.fiscalAdapter = new jqx.dataAdapter(this.fiscal_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }
  ngAfterViewInit(): void 
  {
    if(this.isData['validation_method'] == 'FIFO'){
      this.fifo.check();
    }
    else if(this.isData['validation_method'] == 'LIFO'){
     this.lifo.check();
    }
    else{
      this.wa.check();
    }
  //   if(this.isData['auto_generate_grn_no'] == true){
  //    // this.inventoryForm['auto_generate_grn_no'].checked=true;
  //   }
  //   if(this.isData['auto_generate_missue_no'] == true){
  //     //this.inventoryForm['auto_generate_missue_no'].checked=true;
  //   }
  this.myComboBox.selectItem(this.isEditData.fiscal_year_id);
  this.cdr.detectChanges();
  
  }
  createForm() {
    this.inventoryForm = this.fb.group({
         // 'fiscal_year_id': [null,Validators.required],
          'purchase_order_expiry': [null,Validators.required],
          'auto_generate_req_no': [null,Validators.required],
          'auto_generate_po_no': [null,Validators.required],
          'auto_generate_grn_no': [null,Validators.required],
          'auto_generate_missue_no': [null,Validators.required],
          'pre_req_no': [null],
          'suf_req_no': [null],
          'last_req_no': [null],
          'pre_po_no': [null],
          'suf_po_no': [null],
          'last_po_no': [null],
          'pre_grn_no': [null],
          'suf_grn_no': [null],
          'last_grn_no': [null],
          'pre_missue_no': [null],
          'suf_missue_no': [null],
          'last_missue_no': [null],
          'validation_method': [null,Validators.required],
         });
      }
      saveBtn(post){ 
        if(post['purchase_order_expiry']== true){
           post['purchase_order_expiry']=1;
        }
        else{
          post['purchase_order_expiry']=0
        }
        if(post['auto_generate_req_no'] == true){
          post['auto_generate_req_no']=1;
       }
       else{
         post['auto_generate_req_no']=0
       }
       if( post['auto_generate_po_no'] == true){
        post['auto_generate_po_no']=1;
     }
     else{
       post['auto_generate_po_no']=0
     }
     if( post['auto_generate_grn_no'] == true){
      post['auto_generate_grn_no']=1;
    }
    else{
     post['auto_generate_grn_no']=0
    }
    if(post['auto_generate_missue_no']== true){
      post['auto_generate_missue_no']=1;
    }
    else{
     post['auto_generate_missue_no']=0
    }
    post['fiscal_year_id']=this.myComboBox.val();
    this.myValidator.validate(document.getElementById('inventoryForm'));
    if (post['fiscal_year_id']  ) {
         this.jqxLoader.open();
        this.isService.update(this.isEditData.id,post).subscribe(
          result => {
            let data = this.ehs.formatError(result);
            if (data['result'] == true) {
              this.UpdatedData.emit(data);
            } else {
              let messageDiv: any = document.getElementById('windowMessage');
              messageDiv.innerHTML = '';
              for (let i = 0; i < data['message'].length; i++) {
                let errorDiv = document.createElement('li');
                errorDiv.innerText = data['message'][i];
                messageDiv.appendChild(errorDiv);
              }
              this.winNotification.open();
            }
            this.jqxLoader.close();
          },
          error => {
            let data = this.ehs.formatError(error);
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerText = data['message'];
            this.winNotification.open();
            this.jqxLoader.close();
        }
        );
      }
       }
       cancelBtn(){
        this.cancelData.emit(true);
       }
       rules=[
        { input: '.fiscal_year_id', message: 'Fiscal year required!', action: 'keydown,blur',
         rule: (input: any, commit: any): any => {
          let data = this.myComboBox.val();
          let result = (data!='')?true:false;
          return result;
        }
       },
       ];

}



