import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { InventorySetupRoutingModule } from './inventory-setup-routing.module';
import { InventorySetupAddComponent } from './inventory-setup-add/inventory-setup-add.component';
import { InventorySetupComponent } from './inventory-setup.component';
import { InventorySetupEditComponent } from './inventory-setup-edit/inventory-setup-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    InventorySetupRoutingModule,
  ],
  entryComponents:[
    InventorySetupAddComponent,
    InventorySetupEditComponent
  ],

schemas: [ NO_ERRORS_SCHEMA ],
  declarations: [
    InventorySetupAddComponent,
     InventorySetupComponent, 
     InventorySetupEditComponent
    ]
})
export class InventorySetupModule { }



