import { Component, OnInit, Output, EventEmitter, ViewChild, ElementRef, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { InventorySetupService, CurrentUserService,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxRadioButtonComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxradiobutton';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-inventory-setup-add',
  templateUrl: './inventory-setup-add.component.html',
  styleUrls: ['./inventory-setup-add.component.scss']
})
export class InventorySetupAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('fifo') fifo: jqxRadioButtonComponent;
  @ViewChild('lifo') lifo: jqxRadioButtonComponent;
  @ViewChild('wa') wa: jqxRadioButtonComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('pCheckBox') pCheckBox: jqxCheckBoxComponent;
  @ViewChild('aggCheckBox') aggCheckBox: jqxCheckBoxComponent;
  @ViewChild('agmCheckBox') agmCheckBox: jqxCheckBoxComponent;
  @ViewChild('agpCheckBox') agpCheckBox: jqxCheckBoxComponent;
  @ViewChild('agrCheckBox') agrCheckBox: jqxCheckBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  inventoryForm: FormGroup;
  apiUrl: string;
  fiscal_data: any;
  fiscalAdapter: any;

  userData:  any = {};

  constructor(
    private fb: FormBuilder,
    private isService: InventorySetupService,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    @Inject('API_URL') apiUrl: string) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }

  ngOnInit() {
    this.fiscal_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'fiscal_year?limit=all',
      };
    this.fiscalAdapter = new jqx.dataAdapter(this.fiscal_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }

  createForm() {
    this.inventoryForm = this.fb.group({
      // 'fiscal_year_id': [null,Validators.required],
      // 'purchase_order_expiry': [null,Validators.required],
      // 'auto_generate_req_no': [null,Validators.required],
      // 'auto_generate_po_no': [null,Validators.required],
      // 'auto_generate_grn_no': [null,Validators.required],
      // 'auto_generate_missue_no': [null,Validators.required],
      'pre_req_no': [null],
      'suf_req_no': [null],
      'last_req_no': [null],
      'pre_po_no': [null],
      'suf_po_no': [null],
      'last_po_no': [null],
      'pre_grn_no': [null],
      'suf_grn_no': [null],
      'last_grn_no': [null],
      'pre_missue_no': [null],
      'suf_missue_no': [null],
      'last_missue_no': [null],
      'valuation_method': [null],
    });
  }
  saveBtn(post) {
    this.pCheckBox.val() == true ?  post['purchase_order_expiry'] = 1 :  post['purchase_order_expiry'] = 0; 
    this.agrCheckBox.val() == true?  post['auto_generate_req_no'] = 1 :  post['auto_generate_req_no'] = 0; 
    this.agpCheckBox.val() == true?  post['auto_generate_po_no'] = 1 :  post['auto_generate_po_no'] = 0; 
    this.aggCheckBox.val() == true?  post['auto_generate_grn_no'] = 1 :  post['auto_generate_grn_no'] = 0; 
    this.agmCheckBox.val() == true?  post['auto_generate_missue_no'] = 1 :  post['auto_generate_missue_no'] = 0; 
    post['fiscal_year_id'] = this.myComboBox.val();
    this.myValidator.validate(document.getElementById('inventoryForm'));
    if (post['fiscal_year_id'] && post['purchase_order_expiry'] && post['auto_generate_req_no'] && post['auto_generate_po_no'] && post['auto_generate_grn_no'] && post['auto_generate_missue_no']) {
      this.jqxLoader.open();
      console.log(post);
      this.isService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }

  rules =
    [
      {
        input: '.fiscal_year_id', message: 'Fiscal year required!!', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.myComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.purchase_order_expiry', message: '**', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.pCheckBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.auto_generate_req_no', message: '**', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.agrCheckBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.auto_generate_po_no', message: '**', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.agpCheckBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.auto_generate_grn_no', message: '**', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.aggCheckBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.auto_generate_missue_no', message: '**', action: 'keydown, blur', rule: (input: any, commit: any): any => {

          let data = this.agmCheckBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    //  { input: '.validation_method', message: 'Validation method required!', action: 'keydown, blur', rule: 'required' },



    ];
  }


